import React from 'react';

export class Temp extends React.Component {
  constructor(props)
  {

    super(props);
    this.state={MyToken:"My token"};
    this.state={MyKeyValue:0};
 
    this.checkToken=this.checkToken.bind(this);
    this.keyValuePair=this.keyValuePair.bind(this);
    this.myRef=React.createRef();
    this.FocusYale=this.FocusYale.bind(this);
  }

  FocusYale()
  {
    this.myRef.current.focus();
  }

  keyValuePair()
  {
      var dist={};
      dist={v1:10};
      delete dist.v1;
      if (dist.v1===undefined)  this.setState({MyKeyValue:101})
  }


  checkToken()
  {
    const lToken=localStorage.getItem('token');
    if(lToken)
    {
      this.setState({MyToken:"Existed Token"});

    }
    else
    {
      this.setState({MyToken:"No Token"});

    }
  }



  render()
  {
    return (<div>
      <button onClick={this.checkToken}>Auth Token</button>
      <label>{this.state.MyToken}</label>

      <button onClick={this.keyValuePair}>Key Value</button>
      <label>{this.state.MyKeyValue}</label>

      <button onClick={this.FocusYale}>Focus yale</button>

      <MyTextInput ref={this.myRef}></MyTextInput>

    </div>);
  }
}



const MyTextInput=React.forwardRef((props,ref)=>{return <input type="text" placeholder="yale" ref={ref}></input>});
