import React, { Component } from 'react';
import {Link} from "react-router-dom";


export class ImageSearch extends Component {

  constructor(props) {
    super(props);

    this.state = {
      ArrImage:[],
      IsLoaded:false
    };
  }

  async componentDidMount(){
     const res= await fetch('https://jsonplaceholder.typicode.com/photos');
    const dat = await res.json();
    this.setState({ArrImage:dat,IsLoaded:true});
  }

  render(){
    if(!this.state.IsLoaded)
    {
      return (<p>Loading</p>);
    }
    return (<div>
        <TxtSearch SearchSpace={this.state.ArrImage}></TxtSearch>
      <InfoTips></InfoTips>
    </div>
    ); 
  }
}

class TxtSearch extends React.Component{
	constructor(props)
	{
    super(props);
    this.state={Search:''};
		this.handleChange=this.handleChange.bind(this);
	}
	handleChange(e)
	{
		const hSearch=e.target.value.substr(0,10);
		this.setState({Search:hSearch});
	}
	render(){
    let SeachResult=this.props.SearchSpace.filter((itm)=>{return itm.title.toLowerCase().indexOf(this.state.Search.toLowerCase())!==-1;});
		return (<div>
    <div className="search">
			 <input type="text" value={this.state.Search} placeholder="search" onChange={this.handleChange}/>
       <label>  {SeachResult.length}  items found</label></div>
			
        {SeachResult.length>0 && <DisplaySearch displays={SeachResult}></DisplaySearch>}

      </div>
		);
	}
}

class DisplaySearch extends Component {
  constructor(props) {
    super(props);

    this.state = {
      Start:0
    };
  }

  RecordView(i)
  {
     const cur=this.state.Start;
     this.setState({Start:cur+i});
  }
  
  render(){
    return (
      <div>
        <div className="prevnext"><button onClick={()=>this.RecordView(-10)} disabled={this.state.Start>0?false:true}>Prev 10</button>
        <label>{this.state.Start+1} to {this.state.Start+10}</label>
        <button  onClick={()=>this.RecordView(10)} disabled={this.state.Start>this.props.displays.length-11?true:false}>Next 10</button>
        </div>
        <ShowImages ArrInput={this.props.displays.slice(this.state.Start,this.state.Start+10)}> </ShowImages>
        <div className="placeorder">Click to see the details and place order</div>

      </div>
    ); 
  }
}


function ShowImages(props)
{
    return (<div>
      <ul>
      {props.ArrInput.map((item)=> 
          <li key={item.id.toString()} className="imageli" >
          <Link to={"/ProductDetail/"+item.id}>
          <img src={item.thumbnailUrl} alt="product" width="100" height="100"></img>
          </Link>
            </li>
      )}
      </ul>
      </div>
    );
}


function InfoTips(props){
	return(<div className="manual"> 
		<div className="info">
			<h3>Search, partial display</h3>
			<h3>JSON api</h3>
		</div>
		<div className="tips">
			<h3>Try the search box</h3>
      <h3>Navigate the search results</h3>
		</div>
	</div>);
}



