import React from 'react';
import {Shopping} from "../Authroute"


export class ProductDetail extends React.Component{
constructor(props)
{
	super(props);
	this.state={IsLoaded:false};
}
GoBack()
{
	this.props.history.goBack();
}
async componentDidMount(){
	const res= await fetch('https://jsonplaceholder.typicode.com/photos/'+this.props.match.params.id);
   const dat = await res.json();
   this.setState({pItem:dat,IsLoaded:true});
 }

render(){
	return (<div>
	<div>
	{this.state.IsLoaded && <DetProduct prod={this.state.pItem} > </DetProduct>}
	<CartOption itemKey={"itemkey"+this.props.match.params.id}></CartOption>

	<button onClick={this.GoBack.bind(this)} className="mybutton">Go back to product list and continue shopping</button>
	</div>
	<div>
		<InfoTips></InfoTips>
	</div>
	</div>);
	}
}

function DetProduct(props)
{
	return (
		<div className="pcontainer">
			<div className="pthumb"><img src={props.prod.thumbnailUrl} alt="image" width="300" height="300"></img></div>
			<div className="pcontent">
				<h3 className="ptitle">albumId:{props.prod.albumId}</h3>
				<h3>id:{props.prod.id}</h3>
				<p>title:{props.prod.title}</p>
				<p>url:{props.prod.url}</p>
			</div>
		</div>
	);
}

class CartOption extends React.Component{
	constructor(props)
	{
		super(props);
		this.state={Count:Shopping.getCartItem(this.props.itemKey)};
		this.addItem=this.addItem.bind(this);
		this.removeItem=this.removeItem.bind(this);
	}
	addItem()
	{
		Shopping.addCartItem(this.props.itemKey)
		const oCount=this.state.Count;
		this.setState({Count:oCount+1});
	}
	removeItem()
	{
		Shopping.removeCartItem(this.props.itemKey)
		const oCount=this.state.Count;
		this.setState({Count:oCount-1});
	}
	render(){
		return(<div class="addremove">
			<button onClick={this.addItem}>{this.state.Count>0?'Add':'Buy'}({this.state.Count} )</button>
			{this.state.Count>0 && <button onClick={this.removeItem}>Remove({this.state.Count})</button>}
		</div>);
	}
}


function InfoTips(props){
	return(<div className="manual">  
		<div className="info">
			<h3>Add to or remove from the shopping cart</h3>
			<h3>Go back to product list</h3>
		</div>
		<div className="tips">
			<h3>Try buy (changed to add after first click)</h3>
			<h3>Remove button dispear if no item in cart</h3>
			<h3>Click Cart on the top</h3>
		</div>
	</div>);
}


