import React from "react";
import {Route,Redirect} from "react-router-dom";

export const fakeAuth = {
    isAuthenticated: false,
    authenticate(cb,email,password) {
        if(password.length<5)
        {
            this.isAuthenticated = false;
            setTimeout(cb(false), 100);
        }
        else
        {
            this.isAuthenticated = true;
            setTimeout(cb(true), 100);
        }
    },
    signout(cb) {
      this.isAuthenticated = false;
      setTimeout(cb(false), 100);
    }
  }

export const AuthenticRoute = ({ component: Component, ...rest})=>{
    return (
        <Route 
        {...rest}
        render={props=>{
            if(fakeAuth.isAuthenticated){
                return (<Component {...props}/>);
            }
            else{
                return (
                    <Redirect to={{
                        pathname:"/EmailLogin",
                        state:{
                            from:props.location
                        }
                    }}
                    />
                );
            }
        }
        }
        />
    );
};

export const Shopping={
    dist:{},
    addCartItem(key){
        var tmp=this.dist[key];
        if(tmp===undefined)
        {
            this.dist[key]=1;
        }
        else
        {
            this.dist[key]=tmp+1;
        }
    },
    removeCartItem(key){
        var tmp=this.dist[key];
        if(tmp<2)
        {
            delete this.dist[key];
        }
        else
        {
            this.dist[key]=tmp-1;
        }
    },
    getCartItem(key){
        var tmp=this.dist[key];
        return tmp===undefined?0:tmp;

    },
    GetCart()
    {
        return Object.entries(this.dist);
    }

}
